<template>
    <form action="" class="form" @submit="checkLogin">
      <div class="errorLogin disable">
        <p class="errorLogin__text" id="errorMsg">
          Your username and/or password are incorrect
        </p>
      </div>
      <div class="form__group">
        <div class="linking">
          <label for="Username" class="form__label">Username</label>
        </div>
        <input
          type="text"
          class="form__input"
          placeholder="Username"
          id="Username"
          required
          v-model="username"
        />
      </div>
  
      <div class="form__group">
        <div class="linking">
          <label for="Password" class="form__label">Password</label>
        </div>
        <input
          type="password"
          class="form__input valid"
          placeholder="8+ Characters"
          id="Password"
          required
          @change="
            passwordTouched = true;
            checkPassword();
          "
          @keyup="checkPassword"
          v-model="password"
        />
        <p class="errorMessage disable" id="passwordError">
            Password must contain: upper case, number and 6 characters !
        </p>
      </div>
      <div class="form__group">
        <div class="linking">
          <label for="ConfirmPassword" class="form__label">Confirm Password</label>
        </div>
        <input
          type="password"
          class="form__input valid"
          placeholder="8+ Characters"
          id="ConfirmPassword"
          required
          @change="
            confirmPasswordTouched = true;
            confirmPassword();
          "
          @keyup="confirmkPassword"
          v-model="confirm"
        />
        <p class="errorMessage disable" id="confirmPasswordError">
            The confirmation must be identical to the password !
        </p>
      </div>
      <button
        class="form__btn"
        id="submit"
        :disabled="!isValid"
        :class="{ avalible: isValid, notAvalible: !isValid }"
      >
        Register
      </button>
    </form>
</template>
  
<script>
  export default {
    name: "registerForm",
    data() {
      return {
        username: "",
        password: "",
        confirm: "",
        usernameValid: true,
        passwordValid: false,
        confirmPasswordValid: false,
        passwordTouched: false,
        confirmPasswordTouched: false,
      };
    },
    methods: {
      checkUpperCase(str) {
        return /[A-Z]/.test(str);
      },
      checkNumberCase(str) {
        return /[0-9]/.test(str);
      },
      checkPassword() {
        if (
          this.password.length >= 6 &&
          this.checkUpperCase(this.password) &&
          this.checkNumberCase(this.password)
        ) {
          document.getElementById("Password").classList.remove("notValid");
          document.getElementById("Password").classList.add("valid");
          document.getElementById("passwordError").classList.add("disable");
          this.passwordValid = true;
        } else if (this.passwordTouched) {
          document.getElementById("Password").classList.remove("valid");
          document.getElementById("Password").classList.add("notValid");
          document.getElementById("passwordError").classList.remove("disable");
          this.passwordValid = false;
        }
      },
      confirmPassword() {
        if (
          this.confirm === this.password
        ) {
          document.getElementById("ConfirmPassword").classList.remove("notValid");
          document.getElementById("ConfirmPassword").classList.add("valid");
          document.getElementById("confirmPasswordError").classList.add("disable");
          this.confirmPasswordValid = true;
        } else if (this.confirmPasswordTouched) {
          document.getElementById("ConfirmPassword").classList.remove("valid");
          document.getElementById("ConfirmPassword").classList.add("notValid");
          document.getElementById("confirmPasswordError").classList.remove("disable");
          this.confirmPasswordValid = false;
        }
      },
      checkLogin(e) {
        e.preventDefault();
        fetch('http://localhost:3000/user'
        ,{
            method: 'POST',
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
              "Accept" : "*/*",
            },
            body : "nickname=" + this.username + "&password=" + this.password,
            mode : 'cors'
          }
        )
          .then((res) => {
            if (res.status === 201) {
              res.text().then((ans) => {
                localStorage.token = ans;
                localStorage.username = this.username;
                location.reload();
                this.$router.push({ name: "Home" });
              })
            } else {
              document.getElementById("errorMsg").classList.remove("disable");
            }
            console.log(res);
          })
          .catch((err) => {
            console.log(err);
          });
      },
    },
    computed: {
      isValid() {
        return this.passwordValid && this.username && this.confirmPasswordValid;
      },
    },
  };
  </script>
  <style lang="scss" scoped>
  a:focus {
    outline: none;
  }
  .linking {
    width: 100%;
