    }
  }
  .form {
    width: 100%;
    &__group {
      &:not(:last-child) {
        margin-bottom: 1.5rem;
      }
    }
    &__input {
      height: 2rem;
      width: 100%;
      padding: 2rem 1rem;
      border-radius: 4px;
      color: #47B5FF;
      border: 1px solid white;
      font-size: 1.5rem;
      &:focus,
      &:active {
        outline: none;
      }
      &:placeholder-shown,
      &::placeholder {
        color: white;
      }
    }
    &__label {
      font-size: 1.5rem;
      margin-bottom: 0.5rem;
      color: #47B5FF;
    }
    &__btn {
      width: 100%;
      color: #DFF6FF;
      padding: 1rem;
      border-radius: 4px;
      margin-bottom: 1rem;
      cursor: pointer;
    }
  }
  .errorLogin {
    border-radius: 4px;
    margin: 0.5rem 0;
    padding: 1rem 1.5rem;
    &__text {
      font-size: 1.5rem;
      color: #256D85;
    }
  }
  .errorMessage {
    color: red;
    font-size: 1.2rem;
    margin-top: 1rem;
  }
  .notAvalible {
    background-color: #256D85;
    cursor: default;
  }
  .avalible {
    background-color: #47B5FF;
    cursor: pointer;
  }
  .notValid,
  .notValid:focus,
  .notValid:active {
    border-color: red;
  }
  .valid:focus,
  .valid:active {
    border-color: #DFF6FF;
    box-shadow: inset 0 0 4px 0 #DFF6FF;
  }
  .disable {
    display: none;
  }
  </style>